@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;800&display=swap);
*{box-sizing:border-box}body{padding:0;margin:0;font-family:'Montserrat', sans-serif;color:#202020}h1,h2,h3,h4,h5,h6{margin:0px}section{padding:3.5rem 0.25rem 0.5rem}.card-page-container{height:100vh;display:flex;flex-direction:column;align-items:center;overflow:hidden;background-image:linear-gradient(#fff 0px, #f5f7fa)}.upper-container{flex-grow:1;width:100%;display:flex;justify-content:center;align-items:center;z-index:2}.cards-container{position:relative;width:100%;height:100%;max-width:450px;min-width:300px;min-height:450px;max-height:650px;border-radius:0.5rem;box-shadow:0 2px 10px 0 rgba(136,136,136,0.77);flex-grow:1}.card-container{position:absolute;width:100%;height:100%;background-color:#FAFAFA;border-radius:0.5rem;overflow:hidden;display:flex}.card-swiped-right{transition:0.4s;transform:translateX(150vw) !important}.card-swiped-left{transition:0.4s;transform:translateX(-150vw) !important}.reseting{transition:0.25s}.bottom-action-bar{display:flex;width:100%;justify-content:space-around;padding:1rem;align-items:center;max-width:450px}.bottom-action-bar button{background-color:#FAFAFA}.bottom-action-bar .undo-container button{width:3rem;height:3rem}.bottom-action-bar .undo-container svg{fill:#f5d020}.bottom-action-bar .like-container svg{fill:#63D471}.bottom-action-bar .dislike-container svg{fill:#fd5068}.snackbar-container{color:#FEFEFE;padding:.75rem 5rem;border-radius:0.5rem}.liked-cats-page{max-width:600px;margin:0 auto}.liked-cats-page h2{text-align:center;margin:1rem}.liked-cats-container{display:grid;grid-template-columns:repeat(auto-fit, minmax(10rem, 1fr));grid-gap:0.375rem;gap:0.375rem}.liked-cat-container{min-height:12rem;display:flex;border-radius:5%;overflow:hidden;position:relative}.liked-cat-container .close-container{z-index:3;position:absolute;right:0;top:0}.liked-cat-container .close-container span{border-radius:100%;color:#FEFEFE;overflow:hidden}.liked-cat-container .close-container span svg{background-color:#fd5068}.cat-container{flex-grow:1;background-position:center;background-repeat:no-repeat;background-size:cover;position:relative;display:flex;justify-content:center;align-items:center}.cat-container .mask{background:linear-gradient(10deg, #404040 0%, rgba(64,64,64,0) 40%);height:100%;width:100%;display:flex;flex-direction:column;padding:0.5rem}.cat-container .placeholder{flex-grow:1}.cat-container .cat-details{color:white}.cat-container .cat-header{display:flex;align-items:center;margin-bottom:0.5rem}.cat-container .cat-header h3{font-weight:800;margin-right:1rem;font-size:1.5rem}.cat-container .cat-distance{font-size:1.125rem}header{background-color:#FFF !important}header svg{fill:#999 !important}header .Mui-selected svg{fill:#fd5068 !important}.user-pref-page{display:flex;flex-direction:column;align-items:center}.user-pref-page h3{margin:1rem 0}.user-pref-page .cat-img-container{margin:0.75rem;border-radius:0.375rem;overflow:hidden;max-width:30rem}.user-pref-page .cat-img-container img{width:100%}.slider-container{padding:1rem;max-width:450px;width:100%}.cat-age-container,.max-radius-container{margin-bottom:1rem}.cat-age-container .header,.max-radius-container .header{display:flex;justify-content:space-between}.about-page{padding:3.5rem 0.5rem 0.5rem;margin:0 auto;max-width:600px}.about-page>div{margin-bottom:2rem}.about-page h2{text-align:center}.about-page h2 span{color:#fd5068}.about-page a{color:#fd5068;text-decoration:none}.about-page span{font-weight:800}.about-page ul{list-style:none}.easter-egg{display:flex}.easter-egg img{width:100%}.location-error-container{padding:1rem 2rem;display:flex;flex-direction:column;align-items:center}

